/*----------------------------------------------------------------------------------*/
/*------------------------------RESET DEFAULT STYLES--------------------------------*/
/*----------------------------------------------------------------------------------*/
html,
body {
  width: 100%;
  min-height: 100%;
}
body {
  padding: 0;
  margin: 0;
  font: normal 16px 'Open Sans';
  background: #fff;
  color: #666;
}
div,
p,
a,
span,
em,
strong,
img,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
table,
td,
tr,
form,
fieldset,
label,
select,
input,
textarea {
  padding: 0;
  margin: 0;
  outline: none;
  list-style: none;
}
select,
input,
textarea {
  font: normal 12px 'Open Sans';
  color: #222;
  vertical-align: middle;
}
textarea {
  vertical-align: top;
}
:focus {
  outline: none;
}
input[type=submit] {
  cursor: pointer;
}
img,
fieldset {
  border: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a:link,
a:visited {
  font: 16px "Open Sans", sans-serif;
  color: #222;
  text-decoration: none;
  outline: none;
}
a:hover {
  text-decoration: none;
  outline: none;
}
/*----------------------------------------------------------------------------------*/
/*-------------------------------------DEFAULT CLASSES------------------------------*/
/*----------------------------------------------------------------------------------*/
.float-r {
  float: right !important;
}
.float-l {
  float: left !important;
}
.no-float {
  float: none !important;
}
.no-border {
  border: 0 !important;
}
.no-bg {
  background: none !important;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.no-display {
  display: none !important;
}
.display {
  display: block !important;
}
.inline {
  display: inline !important;
}
.overflow {
  overflow: hidden !important;
}
.align-r {
  text-align: right !important;
}
.align-l {
  text-align: left !important;
}
.align-c {
  text-align: center !important;
}
.clear {
  clear: both;
}
.visible {
  visibility: visible;
}
